import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router";
import styled from "styled-components";
import axios from "axios";

// import "./Detail.scss";
let Box = styled.div`
  padding: 20px;
`;
let Title = styled.h4`
  font-size: 30px;
  color: ${(props) => props.whatColor};
`;

function Detail({ shoes, stock, setStock }) {
  let [fadeIn, setFadeIn] = useState(true);
  let [input, setInput] = useState("");
 
  useEffect(() => {
    axios
      .get("https://codingapple1.github.io/shop/data2.json")
      .then((result) => {
        console.log("컴포넌트 등장시/업데이트시 실행됩니다");
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  useEffect(() => {
    //mount,update 후 (재렌더링됨)
    let timer = setTimeout(() => {
      setFadeIn(false);
    }, 2000);

    //컴포넌트 사라질때
    return function unmount() {
      clearTimeout(timer);
    };
  }, [input]);
  //[] 안의 요소들이 변경될때만 업데이트된다.
  //빈 [] 배열일 시 아무것도 업데이트 되지 않는다.

  let { id } = useParams(); //사용자가 주소창에 친 숫자
  //그런데 가격순, 이름순 등으로 정렬하다보면 주소창에 친 숫자와
  //상품의 고유 아이디가 어긋나게 된다
  //주소창에 친 숫자와 같은 고유아이디를 가진 상품을 보여줘야 한다
  let clickedProduct = shoes.find((item) => {
    return item.id == id;
  });

  let history = useHistory();
  return (
    <div className="container">
      <Box>
        <Title whatColor="#ff0000">Detail</Title>
        {/* <p className="blue">blue</p> */}
      </Box>
      <input
        type="text"
        onChange={(e) => {
          setInput(e.target.value);
        }}
      />
      {input}
      {fadeIn === true ? (
        <div className="alertBox">
          <p>품절 임박! 재고가 얼마 남지 않았습니다.</p>
        </div>
      ) : null}
      <div className="row">
        <div className="col-md-6">
          <img
            src={`https://codingapple1.github.io/shop/shoes${
              clickedProduct.id + 1
            }.jpg`}
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{clickedProduct.title}</h4>
          <p>{clickedProduct.content}</p>
          <p>{clickedProduct.price}</p>

          <Stock stock={stock} setStock={setStock} />

          <button
            className="btn btn-danger"
            onClick={() => {
              setStock([9, 11, 12]);
            }}
          >
            주문하기
          </button>
          <button
            className="btn"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Stock({ stock }) {
  return <p>재고: {stock} </p>;
}
export default Detail;
