import React, { useState } from "react";
import { Navbar, Button, Container, Nav, Carousel } from "react-bootstrap";
import "./App.css";
import shoeData from "./shoeData";
import { Link, Route, Switch } from "react-router-dom";
import Detail from "./Detail";
import axios from "axios";

function App() {
  let [shoes, changeShoes] = useState(shoeData);
  let [loading, setLoading] = useState(false);
  let [stock, setStock] = useState([10, 11, 12]);

  return (
    <div className="App">
      <Navbar bg="primary" variant="dark">
        <Container>
          <Navbar.Brand href="#home">Navbar</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to="/detail">
              Detail
            </Nav.Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      <Carousel>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="https://file.mk.co.kr/meet/neds/2021/05/image_readtop_2021_515334_16221769004661509.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>First slide label</h3>
            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="https://t1.kakaocdn.net/kakaoenterprise_com/images/img_kakaowork02.jpg"
            alt="Second slide"
          />

          <Carousel.Caption>
            <h3>Second slide label</h3>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="https://a.ksd-i.com/s/800_86400_ffdcd376ae6c60ce7a916b7f2c87faa4/static.koreastardaily.com/2017-05-16/94537-512838.jpg"
            alt="Third slide"
          />

          <Carousel.Caption>
            <h3>Third slide label</h3>
            <p>
              Praesent commodo cursus magna, vel scelerisque nisl consectetur.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>

      <Switch>
        <Route exact path="/">
          <div className="container">
            <div className="row">
              {shoes.map((item, index) => {
                return <Col shoes={shoes} index={index} key={index} />;
              })}
            </div>
            {loading === true ? <div>로딩중입니다...</div> : null}
            <button
              className="btn btn-primary"
              onClick={() => {
                setLoading(true);
                axios
                  .get("https://codingapple1.github.io/shop/data2.json")
                  .then((result) => {
                    setLoading(false);
                    // let plused = [...shoes].concat(result.data);
                    // console.log(plused);
                    // changeShoes(plused);

                    changeShoes([...shoes, ...result.data]);
                  })
                  .catch((err) => {
                    setLoading(false);

                    console.log(err);
                  });
              }}
            >
              더보기
            </button>
          </div>
        </Route>
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} setStock={setStock} />
        </Route>
        <Route path="/:id">
          <div>아무 문자열 적었을때</div>
        </Route>
      </Switch>
    </div>
  );
}

export default App;

function Col({ shoes, index }) {
  return (
    <div className="col-md-4">
      <img
        className="w-100"
        src={`https://codingapple1.github.io/shop/shoes${index + 1}.jpg`}
        alt=""
      />
      <h4>{shoes[index].title}</h4>
      <p>{shoes[index].content}</p>
    </div>
  );
}
